___CSS_LOADER_EXPORT___.push([module.id, ".pi-eye[data-v-97d89dda],.pi-eye-slash[data-v-97d89dda]{margin-right:1rem;transform:scale(1.6)}", "",{"version":3,"sources":["webpack://./resources/js/pages/admin/auth/Login.vue"],"names":[],"mappings":"AAkLA,wDAEI,iBAAkB,CADlB,oBAEJ","sourcesContent":["<script>\nimport { useLayout } from \"../../../layouts/Admin/composables/layout\";\nimport AppConfig from \"../../../layouts/Admin/AppConfig.vue\";\nimport store from \"../../../store\";\n\nexport default {\n    name: \"AdminLogin\",\n    components: {\n        AppConfig,\n    },\n    data() {\n        return {\n            loading: false,\n            email: \"\",\n            password: \"\",\n            checked: false,\n            errors: null,\n        };\n    }, //end of data\n\n    computed: {\n        logoUrl() {\n            const { layoutConfig } = useLayout();\n            return `../${layoutConfig.darkTheme.value ? \"Logo\" : \"Logo\"}.svg`;\n        },\n    }, //end of computed\n\n    methods: {\n        login() {\n            this.loading = true;\n            axios\n                .post(\"/api/admin/login\", {\n                    email: this.email,\n                    password: this.password,\n                })\n                .then((response) => {\n                    if (response.status == 200) {\n                        this.$router.push({ name: \"admin.dashboard\" });\n                    }\n                })\n                .catch((errors) => {\n                    this.errors = errors.response.data.errors;\n                })\n                .then(() => {\n                    this.loading = false;\n                });\n        }, // end of login\n\n        forgetPassword() {\n            //relax and try to remember it\n        }, //end of forgetPassword\n    }, //end of methods\n\n    async beforeRouteEnter(to, from, next) {\n        // check if authnticated\n        try {\n            const user = (await axios.get(\"/api/admin/user\")).data;\n            store.commit(\"adminAuth/login\", user);\n            next({ name: \"admin.dashboard\" });\n        } catch (error) {\n            if (error.response.status == 401) {\n                next();\n                // this.$router.push({ name: \"admin.login\" });\n            }\n        }\n    }, //end of beforeRouteEnter\n};\n</script>\n\n<template>\n    <div\n        class=\"surface-ground flex align-items-center justify-content-center min-h-screen min-w-screen overflow-hidden\"\n    >\n        <div class=\"flex flex-column align-items-center justify-content-center\">\n            <img\n                :src=\"logoUrl\"\n                alt=\"Admin logo\"\n                class=\"mb-5 w-6rem flex-shrink-0\"\n            />\n            <div\n                style=\"\n                    border-radius: 56px;\n                    padding: 0.3rem;\n                    background: linear-gradient(\n                        180deg,\n                        var(--primary-color) 10%,\n                        rgba(33, 150, 243, 0) 30%\n                    );\n                \"\n            >\n                <div\n                    class=\"w-full surface-card py-8 px-5 sm:px-8\"\n                    style=\"border-radius: 53px\"\n                >\n                    <div class=\"text-center mb-5\">\n                        <div class=\"text-900 text-3xl font-medium mb-3\">\n                            Welcome, Asa7bi!\n                        </div>\n                        <span class=\"text-600 font-medium\"\n                            >Sign in to continue</span\n                        >\n                    </div>\n                    <div>\n                        <label\n                            for=\"email\"\n                            class=\"block text-900 text-xl font-medium mb-2\"\n                            >Email</label\n                        >\n                        <InputText\n                            id=\"email\"\n                            type=\"text\"\n                            placeholder=\"Email address\"\n                            class=\"w-full md:w-30rem mb-5\"\n                            style=\"padding: 1rem\"\n                            v-model=\"email\"\n                        />\n\n                        <label\n                            for=\"password\"\n                            class=\"block text-900 font-medium text-xl mb-2\"\n                            >Password</label\n                        >\n                        <InputText\n                            id=\"password\"\n                            v-model=\"password\"\n                            placeholder=\"Password\"\n                            type=\"password\"\n                            class=\"w-full mb-3\"\n                            inputClass=\"w-full\"\n                            inputStyle=\"padding:1rem\"\n                        />\n\n                        <div\n                            class=\"flex align-items-center justify-content-between mb-5 gap-5\"\n                        >\n                            <div class=\"flex align-items-center\">\n                                <Checkbox\n                                    v-model=\"checked\"\n                                    id=\"rememberme\"\n                                    binary\n                                    class=\"mr-2\"\n                                ></Checkbox>\n                                <label class=\"m-auto\" for=\"rememberme\"\n                                    >Remember me</label\n                                >\n                            </div>\n                            <Button\n                                class=\"font-medium no-underline ml-2 text-right cursor-pointer\"\n                                label=\"Info\"\n                                severity=\"info\"\n                                text\n                                style=\"color: var(--primary-color)\"\n                                @click.prevent=\"forgetPassword\"\n                                >Forgot password?</Button\n                            >\n                        </div>\n                        <div v-if=\"errors\" class=\"my-2\">\n                            <PageErrors :errors=\"errors\" />\n                        </div>\n                        <Button\n                            label=\"Sign In\"\n                            class=\"w-full p-3 text-xl\"\n                            @click.prevent=\"login\"\n                        ></Button>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <AppConfig simple />\n</template>\n\n<style scoped>\n.pi-eye {\n    transform: scale(1.6);\n    margin-right: 1rem;\n}\n\n.pi-eye-slash {\n    transform: scale(1.6);\n    margin-right: 1rem;\n}\n</style>\n"],"sourceRoot":""}]);
